<style scoped>
.backpanel-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        /* Blank mask for isolating the background element from the rest of the page */
        mask: url("@/assets/blank-mask.svg");
        mask-mode: alpha;
        mask-size: cover;
}
.logo-container {
        margin-top: 8px;
        height: calc(100vh - 8px);
}
.backpanel,
.backpanel-2 {
